













































































































import { Component, Prop, Vue } from "vue-property-decorator";
import LogicFlow from "@logicflow/core";
import { SelectionSelect } from "@logicflow/extension";
import demoData from "../example.json";

@Component
export default class ToolbarComponent extends Vue {
  @Prop() private lf!: LogicFlow;
  undoDisable = true;
  redoDisable = true;
  graphData = null;
  dataVisible = false;
  selectionMode = false;

  mounted(): void {
    this.lf.on("history:change", ({ data: { undoAble, redoAble } }) => {
      this.undoDisable = !undoAble;
      this.redoDisable = !redoAble;
    });
  }

  zoomIn(): void {
    this.lf.zoom(true);
  }

  zoomOut(): void {
    this.lf.zoom(false);
  }

  zoomReset(): void {
    this.lf.resetZoom();
  }

  translateRest(): void {
    this.lf.resetTranslate();
  }

  reset(): void {
    this.lf.resetZoom();
    this.lf.resetTranslate();
  }

  undo(): void {
    this.lf.undo();
  }

  redo(): void {
    this.lf.redo();
  }

  download(): void {
    this.lf.getSnapshot();
  }

  catData(): void {
    this.$emit("catData");
  }

  selection(): void {
    this.selectionMode = !this.selectionMode;
    this.lf.updateEditConfig({
      stopMoveGraph: this.selectionMode,
    });

    if (!this.selectionMode) {
      SelectionSelect.close();
    } else {
      SelectionSelect.open();
    }
  }

  loadDemo(): void {
    this.lf.render(demoData);
  }
}
