    constructor() {
        super(...arguments);
        this.selectRow = null;
        this.selectItem = null;
        this.pageFormRef = "pageForm";
        this.loading = false;
    }
    //事件-增加行
    async handleAddRow() {
        this.page.rows.push({
            fields: [],
        });
    }
    //事件-选中行
    async handleSelectRowClick(row) {
        this.selectRow = row;
    }
    // 事件-删除行
    async handleDeleteRow(row) {
        if (row.fields.length > 0) {
            // 检查是否被公式引用
            // if (!this.validIsRef(row.fields, group.groupName)) return;
            openConfirmModal("提示", "是否删除该行？", () => this.deleteRow(row));
        }
        else {
            this.deleteRow(row);
        }
    }
    //事件-新增字段
    async handleAddField(row, field) {
        if (row.fields.length >= 4) {
            openInfoModal("提示", "本行已有四列，禁止拖放");
        }
        else {
            try {
                this.addField(row, field);
            }
            catch (error) {
                console.log("新增字段出错： " + error);
            }
        }
    }
    //事件-选中字段
    async handleSelectField(row, field) {
        this.selectItem = field;
        this.$emit("selectedField", row, field);
    }
    //事件-移除字段
    async handleRemoveField(row, field) {
        this.deleteField(row, field);
    }
    //事件-移除字段
    async handleDeleteField(row, field) {
        this.deleteField(row, field);
    }
