
















import { Component, Prop, Vue } from "vue-property-decorator";
import LogicFlow from "@logicflow/core";
import {
  ConditionTypeEnum,
  EdgeNameTypeEnum,
  EdgeSchema,
} from "../common/model";
import ConditionSetting from "./condition_rule_setting/index.vue";

@Component({
  components: {
    ConditionSetting,
  },
})
export default class EdgeProperty extends Vue {
  //eslint-disable-next-line
  @Prop() private nodeData!: any;
  @Prop() private lf!: LogicFlow;

  formData: EdgeSchema = {
    name: "",
    enName: EdgeNameTypeEnum.Condition,
    condition: {
      type: ConditionTypeEnum.Default,
      expressions: [],
    },
  };

  mounted(): void {
    const { properties } = this.nodeData;
    if (properties) {
      this.formData = Object.assign({}, this.formData, properties);
    }
  }

  onSubmit(): void {
    const { id } = this.nodeData;
    this.lf.setProperties(id, this.formData);
    this.lf.updateText(id, this.formData.name);
    this.$emit("onClose");
  }
}
